'use client';

import React, { useEffect, useRef, useState } from 'react';
import { useDitherStore } from '@/store/useDitherStore';
import { applyDithering } from '@/lib/dithering';
import GlassCard from './GlassCard';
import ComparisonSlider from './ComparisonSlider';
import PresetManager from './PresetManager';
import KeyboardShortcuts from './KeyboardShortcuts';

export default function DitherCanvas() {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const animationFrameRef = useRef<number>();
  const [isFullscreen, setIsFullscreen] = useState(false);
  const { 
    originalImage, 
    settings, 
    imageDataUrl, 
    showComparison,
    setShowComparison,
    isProcessing,
    setIsProcessing 
  } = useDitherStore();

  // Handle animated filters
  const needsAnimation = ['mesh-gradient', 'dot-orbit', 'warp'].includes(settings.filter);

  useEffect(() => {
    if (!originalImage || !canvasRef.current) return;

    const processImage = () => {
      if (!canvasRef.current) return;
      
      setIsProcessing(true);
      
      // Use requestAnimationFrame for smooth rendering
      requestAnimationFrame(() => {
        if (canvasRef.current) {
          const canvas = canvasRef.current;
          applyDithering(canvas, originalImage, settings);
          setIsProcessing(false);
        }
      });
    };

    if (needsAnimation) {
      // Animate at 30fps for performance
      const animate = () => {
        processImage();
        animationFrameRef.current = setTimeout(() => {
          requestAnimationFrame(animate);
        }, 1000 / 30);
      };
      animate();
    } else {
      processImage();
    }

    return () => {
      if (animationFrameRef.current) {
        clearTimeout(animationFrameRef.current);
      }
    };
  }, [originalImage, settings, needsAnimation, setIsProcessing]);

  const handleDownload = () => {
    if (!canvasRef.current) return;

    const link = document.createElement('a');
    link.download = `ditherlab-${settings.filter !== 'none' ? settings.filter : settings.algorithm}-${Date.now()}.png`;
    link.href = canvasRef.current.toDataURL('image/png');
    link.click();
  };

  const handleDownloadSVG = () => {
    if (!canvasRef.current) return;

    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const data = imageData.data;
    
    let svgContent = `<?xml version="1.0" encoding="UTF-8"?>
<svg xmlns="http://www.w3.org/2000/svg" width="${canvas.width}" height="${canvas.height}" viewBox="0 0 ${canvas.width} ${canvas.height}">`;

    for (let y = 0; y < canvas.height; y++) {
      for (let x = 0; x < canvas.width; x++) {
        const i = (y * canvas.width + x) * 4;
        const r = data[i];
        const g = data[i + 1];
        const b = data[i + 2];
        svgContent += `<rect x="${x}" y="${y}" width="1" height="1" fill="rgb(${r},${g},${b})"/>`;
      }
    }

    svgContent += '</svg>';

    const blob = new Blob([svgContent], { type: 'image/svg+xml' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.download = `ditherlab-${settings.filter !== 'none' ? settings.filter : settings.algorithm}-${Date.now()}.svg`;
    link.href = url;
    link.click();
    URL.revokeObjectURL(url);
  };

  const toggleFullscreen = () => {
    setIsFullscreen(!isFullscreen);
  };

  if (!imageDataUrl) return null;

  return (
    <>
      <GlassCard className="animate-slide-up">
        <div className="flex flex-col lg:flex-row justify-between items-start lg:items-center mb-4 gap-3">
          <h2 className="text-2xl font-semibold text-white">Preview</h2>
          <div className="flex flex-wrap gap-3">
            <PresetManager />
            <KeyboardShortcuts />
            
            <button
              onClick={() => setShowComparison(!showComparison)}
              className={`glass-button px-4 py-2 rounded-xl text-white text-sm font-medium
                       transition-all duration-200 ${showComparison ? 'bg-glassAccent/60' : ''}`}
              title="Toggle Comparison (Space)"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
                      d="M7 21a4 4 0 01-4-4V5a2 2 0 012-2h4a2 2 0 012 2v12a4 4 0 01-4 4zm0 0h12a2 2 0 002-2v-4a2 2 0 00-2-2h-2.343M11 7.343l1.657-1.657a2 2 0 012.828 0l2.829 2.829a2 2 0 010 2.828l-8.486 8.485M7 17h.01" />
              </svg>
            </button>
            
            <button
              onClick={toggleFullscreen}
              className="glass-button px-4 py-2 rounded-xl text-white text-sm font-medium
                       transition-all duration-200"
            >
              {isFullscreen ? 'Exit Fullscreen' : 'Fullscreen'}
            </button>
            <button
              onClick={handleDownload}
              className="glass-button px-4 py-2 rounded-xl text-white text-sm font-medium
                       transition-all duration-200 flex items-center gap-2"
              title="Download PNG (Ctrl+S)"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} 
                      d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
              </svg>
              PNG
            </button>
            <button
              onClick={handleDownloadSVG}
              className="glass-button px-4 py-2 rounded-xl text-white text-sm font-medium
                       transition-all duration-200 flex items-center gap-2"
              title="Download SVG (Ctrl+E)"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} 
                      d="M7 21h10a2 2 0 002-2V9.414a1 1 0 00-.293-.707l-5.414-5.414A1 1 0 0012.586 3H7a2 2 0 00-2 2v14a2 2 0 002 2z" />
              </svg>
              SVG
            </button>
          </div>
        </div>

        {isProcessing && (
          <div className="mb-4 flex items-center gap-2 text-white/70 text-sm">
            <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
            Processing...
          </div>
        )}

        {showComparison ? (
          <ComparisonSlider
            originalSrc={imageDataUrl}
            ditheredCanvas={canvasRef.current}
          />
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Original Image */}
            <div>
              <h3 className="text-lg font-medium text-white/80 mb-3">Original</h3>
              <div className="rounded-xl overflow-hidden bg-black/20 border border-white/10">
                <img 
                  src={imageDataUrl} 
                  alt="Original" 
                  className="w-full h-auto"
                />
              </div>
            </div>

            {/* Processed Image */}
            <div>
              <h3 className="text-lg font-medium text-white/80 mb-3">
                Processed ({settings.filter !== 'none' ? settings.filter : settings.algorithm})
              </h3>
              <div className="rounded-xl overflow-hidden bg-black/20 border border-white/10 relative">
                <canvas
                  ref={canvasRef}
                  className="w-full h-auto"
                />
                {needsAnimation && (
                  <div className="absolute top-2 right-2 glass-button px-2 py-1 rounded text-xs text-white flex items-center gap-1">
                    <span className="w-2 h-2 bg-red-500 rounded-full animate-pulse"></span>
                    LIVE
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </GlassCard>

      {/* Fullscreen Modal */}
      {isFullscreen && (
        <div 
          className="fixed inset-0 z-50 bg-black/90 backdrop-blur-xl flex items-center justify-center p-4"
          onClick={toggleFullscreen}
        >
          <button
            className="absolute top-6 right-6 glass-button p-3 rounded-full text-white z-10"
            onClick={toggleFullscreen}
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} 
                    d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
          <div className="max-w-full max-h-full overflow-auto">
            {showComparison ? (
              <div className="w-[90vw] max-w-6xl">
                <ComparisonSlider
                  originalSrc={imageDataUrl}
                  ditheredCanvas={canvasRef.current}
                />
              </div>
            ) : (
              <canvas
                ref={canvasRef}
                className="max-w-full max-h-[90vh] w-auto h-auto"
              />
            )}
          </div>
        </div>
      )}
    </>
  );
}
